import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Papa from 'papaparse';
import * as utils from './utils.js';


function compile_ical(arrs) {
  var err = true;

  if (arrs.length == 0) {return [err, "EMPTY"]}

  var filtered = new Array();

  const header = utils.compare_two_arrs_of_strings(arrs[0], ['Date', 'Shift', 'Start', 'Finish', 'Oncall From', 'Oncall To', 'Area', 'Role', 'Status']);

  for (var i=0+header; i<arrs.length; i++) {
    if (arrs[i].length == 9) {
      filtered.push(arrs[i]);
    }
  }
  
  if (filtered.length == 0) {return [err, "EMPTY"]}
  
  var ical_output = utils.ical_head;

  for (var i=0; i<filtered.length; i++) {
    var start, end, name;
    if (filtered[i][2]) {
      start = utils.convert_roster_time(filtered[i][2]);
    } else {
      if (filtered[i][4]) {
        start = utils.convert_roster_time(filtered[i][4]);
      } else {continue}
    }

    if (filtered[i][3]) {
      end = utils.convert_roster_time(filtered[i][3]);
    } else {
      if (filtered[i][5]) {
        end = utils.convert_roster_time(filtered[i][5]);
      } else {continue}
    }
    name = filtered[i][1];
    
    ical_output += utils.ical_event(name, start, end);
  }

  ical_output += utils.ical_tail;

  utils.download_ical_file(ical_output, 'roster.ics');
  
}



function readCSV(file) {
  const reader = new FileReader();
  reader.addEventListener("load", (event) => {
    const papa_obj = Papa.parse(reader.result);
    // should implement checking of papa_obj.errors
    console.log(compile_ical(papa_obj.data));
  });
 
  reader.readAsText(file);
  
}

function CSVForm() {
  const [validated, setValidated] = useState(false);

  const handleChange = (event) => {
    document.getElementById("file-upload").setCustomValidity("");
    setValidated(false);
  }

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    const field = document.getElementById("file-upload");
    const file = field.files[0];
    event.preventDefault();

    if (form.checkValidity() === false || file.type !== "text/csv") {
      event.stopPropagation();
      field.setCustomValidity("Invalid");
    } else {
      field.setCustomValidity("");
      readCSV(file);
    }
    setValidated(true);
  };

  return (
    <Form noValidate validated={validated} onSubmit={handleSubmit} onChange={handleChange}>
        <Form.Group className="mb-3" controlId="file-upload">
            <Form.Control type="file" placeholder="Upload file..." required />
            <Form.Control.Feedback type="invalid">
              Please provide a valid CSV file
            </Form.Control.Feedback>
            <Form.Text className="text-muted">
              Click "Submit" and your <code>.ical</code> file should download.
            </Form.Text>
        </Form.Group>
        <Button variant="primary" type="submit" id="CSVFormSubmitBtn">
            Submit
        </Button>
    </Form>
  );
}

export default CSVForm;
  