import { v4 as uuidv4 } from 'uuid';
import moment from 'moment-timezone';

export function compare_two_arrs_of_strings(a, b) {
  if (a.length != b.length || a.length*b.length == 0) {
    return false
  }
  for (var i=0; i<a.length; i++) {
    if (a[i] != b[i]) {
      return false
    }
  }
  return true
}

export function convert_roster_time(t) {
  // input: "18/04/2024 8:30:00 AM"
  // output: Australia/Melbourne:20231101T160000
  const split_1 = t.split(" ");

  var [dd,mm,yyyy] = split_1[0].split('/');
  if (dd.length == 1) {dd = `0${dd}`;}
  if (mm.length == 1) {mm = `0${mm}`;}
  if (yyyy.length == 2) {yyyy = `20${yyyy}`;}

  var [hh,nn,ss] = split_1[1].split(':');
  if (hh.length == 1) {hh = `0${hh}`;}
  if (nn.length == 1) {nn = `0${nn}`;}
  if (ss.length == 1) {ss= `0${ss}`;}

  if (split_1[2] == "PM" && hh != "12") {
    hh = (parseInt(hh) + 12).toString();
  } else if (split_1[2] == "AM" && hh == "12") {
    hh = "00"
  }

  return `Australia/Melbourne:${yyyy}${mm}${dd}T${hh}${nn}${ss}`
}

export function date_time_now() {
  const currentTime = moment().tz("Australia/Melbourne").format("YYYY-MM-DD-HH-mm-ss");
  var [yyyy,mm,dd,hh,nn,ss] = currentTime.split('-');
  return `Australia/Melbourne:${yyyy}${mm}${dd}T${hh}${nn}${ss}`
}

export function ical_event(name, start, end) {
  return "BEGIN:VEVENT\n" +
  `SUMMARY:${name}\n` +
  `UID:${uuidv4()}\n` +
  "SEQUENCE:0\n" +
  "STATUS:CONFIRMED\n" +
  "TRANSP:TRANSPARENT\n" +
  `DTSTAMP;TZID=${date_time_now()}\n` +
  `DTSTART;TZID=${start}\n` +
  `DTEND;TZID=${end}\n` +
  "END:VEVENT\n"
}

